html,
body,
p,
ol,
ul,
li,
dl,
dt,
dd,
blockquote,
figure,
fieldset,
legend,
textarea,
pre,
iframe,
hr,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

ul {
  list-style: none;
}

button,
input,
select {
  margin: 0;
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

img,
video {
  height: auto;
  max-width: 100%;
}

iframe {
  border: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

td,
th {
  padding: 0;
}

:root {
  --font-barlow: Barlow, sans-serif;
  --font-fraunces: "Fraunces 9pt", sans-serif;
  --font-thin: 300;
  --font-normal: 400;
  --font-medium: 500;
  --font-semi-bold: 600;
  --font-bold: 700;
  --font-x-bold: 800;
}

:root {
  --soft-red: hsl(7, 99%, 70%);
  --soft-red-transparent: hsl(7, 99%, 70%, 0.4);
  --yellow: hsl(51, 100%, 49%);
  --yellow-transparent: hsla(51, 100%, 49%, 0.4);
  --dark-desaturated-cyan: hsl(167, 40%, 24%);
  --dark-blue: hsl(198, 62%, 26%);
  --dark-moderate-cyan: hsl(168, 34%, 41%);
  --very-dark-desaturated-blue: hsl(212, 27%, 19%);
  --very-dark-grayish-blue: hsl(213, 9%, 39%);
  --dark-grayish-blue: hsl(232, 10%, 55%);
  --grayish-blue: hsl(210, 4%, 67%);
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
}

html {
  font-size: 55%;
  scroll-behavior: smooth;
  scroll-padding-top: 6rem;
}
html::-webkit-scrollbar {
  width: 0.5rem;
}
html::-webkit-scrollbar-track {
  background: transparent;
}
html::-webkit-scrollbar-thumb {
  background: var(--soft-red);
  border-radius: 15px;
}

scrollbar {
  width: 0.5rem;
}

scrollbar-track {
  background: transparent;
}

scrollbar-thumb {
  background: var(--soft-red);
  border-radius: 15px;
}

body {
  font-family: var(--font-barlow);
  font-size: 1.8rem;
  overflow-x: hidden;
}

.header_container {
  width: 100%;
  height: 100vh;
  background-image: url(../../images/mobile/image-header.jpg);
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  padding: 3.5rem 3rem 0;
}

.navigation {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
}

.navigation__topbar {
  display: none;
}

.navigation .navigation__topbar.active {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 8rem;
  left: 0;
  width: 100%;
  height: 35rem;
  background-color: var(--white);
  transition: 5s;
}
.navigation .navigation__topbar.active::before {
  content: "";
  position: absolute;
  top: -25px;
  right: 0;
  transform: rotate(-89deg);
  border: 25px solid;
  border-color: transparent transparent var(--white) transparent;
}
.navigation .navigation__topbar.active ul {
  text-align: center;
}
.navigation .navigation__topbar.active ul li {
  margin-bottom: 3.2rem;
}
.navigation .navigation__topbar.active ul li a {
  font-size: 2.2rem;
  font-weight: var(--font-bold);
  color: var(--dark-grayish-blue);
}
.navigation .navigation__topbar.active .navigation-btn {
  border: none;
  outline: none;
  font-family: var(--font-fraunces);
  font-weight: var(--font-bold);
  font-size: 1.7rem;
  text-transform: uppercase;
  padding: 1.8rem 2.7rem;
  border-radius: 25px;
  background-color: var(--yellow);
  cursor: pointer;
}

.header__main {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.header__main h1 {
  font-family: var(--font-fraunces);
  font-size: 5rem;
  font-weight: 800;
  color: var(--white);
  letter-spacing: 5px;
  text-transform: uppercase;
  text-align: center;
  margin-top: 25rem;
  margin-bottom: 12.5rem;
}

.main_container {
  min-width: 100%;
}
.main_container .description {
  display: flex;
  flex-direction: column;
}
.main_container .description .description__image {
  width: 100%;
  height: 420px;
  background-image: url(../../images/mobile/image-transform.jpg);
  background-position: center;
  background-size: cover;
}
.main_container .description .description__textbox {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
  padding: 10rem 2rem;
}
.main_container .description .description__textbox .description__textbox-title {
  font-family: var(--font-fraunces);
  font-size: 3.5rem;
  font-weight: var(--font-bold);
  color: var(--black);
  margin-bottom: 2rem;
}
.main_container .description .description__textbox .description__textbox-text {
  font-size: 1.9rem;
  line-height: 1.8;
  font-weight: 600;
  color: var(--dark-grayish-blue);
  margin-bottom: 2rem;
}
.main_container .description .description__textbox .description__textbox-link {
  font-family: var(--font-fraunces);
  font-size: 2.2rem;
  font-weight: var(--font-bold);
  color: var(--black);
  text-transform: uppercase;
  position: relative;
}
.main_container .description .description__textbox .description__textbox-link::before {
  content: "";
  display: inline-block;
  position: absolute;
  bottom: 0;
  left: -8%;
  z-index: -1;
  width: 115%;
  height: 10px;
  background-color: var(--yellow-transparent);
  border-radius: 4px;
  transition: 400ms ease;
}
.main_container .description .description__textbox .description__textbox-link:hover::before {
  background-color: var(--yellow);
}
.main_container .description_2 .description__image {
  width: 100%;
  height: 420px;
  background-image: url(../../images/mobile/image-stand-out.jpg);
  background-position: center;
  background-size: cover;
}
.main_container .description_2 .description__textbox .description__textbox-link::before {
  content: "";
  display: inline-block;
  position: absolute;
  bottom: 0;
  left: -8%;
  z-index: -1;
  width: 115%;
  height: 10px;
  background-color: var(--soft-red-transparent);
  border-radius: 4px;
  transition: 400ms ease;
}
.main_container .description_2 .description__textbox .description__textbox-link:hover::before {
  background-color: var(--soft-red);
}

.design_container {
  width: 100%;
  display: flex;
  flex-direction: column;
}

.graphic_design {
  width: 100%;
  height: 540px;
  background-image: url(../../images/mobile/image-graphic-design.jpg);
  background-position: center 35%;
  background-size: cover;
  display: flex;
  justify-content: center;
  align-items: flex-end;
}

.design__textbox {
  text-align: center;
  padding: 0rem 2rem;
  margin-bottom: 6rem;
}
.design__textbox .design__textbox-title {
  font-family: var(--font-fraunces);
  font-size: 2.8rem;
  font-weight: var(--font-bold);
  text-transform: capitalize;
  color: var(--dark-desaturated-cyan);
  margin-bottom: 3rem;
}
.design__textbox .design__textbox-text {
  font-size: 1.9rem;
  line-height: 1.6;
  font-weight: 600;
  color: var(--dark-desaturated-cyan);
}

.photography {
  width: 100%;
  height: 540px;
  background-image: url(../../images/mobile/image-photography.jpg);
  background-position: center 20%;
  background-size: cover;
  display: flex;
  justify-content: center;
  align-items: flex-end;
}
.photography h1.design__textbox-title {
  color: var(--dark-blue);
}
.photography p.design__textbox-text {
  color: var(--dark-blue);
}

.testimonials {
  width: 100%;
  text-align: center;
  padding: 8rem 2rem;
}
.testimonials .testimonials-title {
  font-family: var(--font-fraunces);
  letter-spacing: 3px;
  font-size: 2.2rem;
  font-weight: var(--font-bold);
  color: var(--grayish-blue);
  text-transform: uppercase;
  margin-bottom: 6rem;
}
.testimonials .testimonials__container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.testimonials .testimonials__container .testimonials__card {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 5rem 0rem;
}
.testimonials .testimonials__container .testimonials__card .testimonials__card-image {
  width: 105px;
  margin-bottom: 4rem;
}
.testimonials .testimonials__container .testimonials__card .testimonials__card-image img {
  border-radius: 50%;
}
.testimonials .testimonials__container .testimonials__card .testimonials__card-text p {
  font-size: 1.9rem;
  font-weight: 600;
  color: var(--very-dark-grayish-blue);
  line-height: 1.6;
}
.testimonials .testimonials__container .testimonials__card .testimonials__card-profile {
  margin-top: 4rem;
}
.testimonials .testimonials__container .testimonials__card .testimonials__card-profile h2 {
  font-family: var(--font-fraunces);
  font-size: 2.1rem;
  font-weight: var(--font-bold);
  color: var(--black);
  margin-bottom: 1.3rem;
}
.testimonials .testimonials__container .testimonials__card .testimonials__card-profile p {
  font-size: 1.5rem;
  font-weight: 500;
  color: var(--grayish-blue);
}

.gallery {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: 1;
}

.footer_container {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-color: var(--dark-moderate-cyan);
  padding: 8rem 0rem;
}
.footer_container .footer__logo {
  margin-bottom: 5rem;
}
.footer_container .footer__logo svg#logo {
  fill: var(--dark-desaturated-cyan);
  transition: fill 400ms ease;
  cursor: pointer;
}
.footer_container .footer__logo svg#logo:hover {
  fill: var(--white);
}

.footer__navigation ul {
  display: flex;
  margin-bottom: 6rem;
}
.footer__navigation ul li:nth-child(2) {
  margin: 0rem 4rem;
}
.footer__navigation ul a {
  font-size: 2.2rem;
  font-weight: 600;
  color: var(--dark-desaturated-cyan);
  transition: color 300ms;
}
.footer__navigation ul a:hover {
  color: var(--white);
}

.footer__social {
  margin-bottom: 3.5rem;
}

.footer__social .social-icon {
  margin: 0rem 1.5rem;
}
.footer__social .social-icon svg {
  fill: var(--dark-desaturated-cyan);
  transition: fill 300ms;
}
.footer__social .social-icon svg:hover {
  fill: var(--white);
}

.attribution {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.attribution p {
  font-size: 2rem;
  line-height: 1.6;
  font-weight: 500;
  color: var(--dark-desaturated-cyan);
}
.attribution p span {
  font-size: 3.2rem;
  margin: 0rem 0.3rem;
}
.attribution p a {
  font-family: var(--font-fraunces);
  font-size: 2.3rem;
  font-weight: var(--font-bold);
  text-transform: uppercase;
  color: var(--white);
}

@media only screen and (min-width: 40em) {
  html {
    font-size: 62.5%;
  }
  body .header_container {
    background-image: url(../../images/desktop/image-header.jpg) !important;
  }
  .navigation__topbar {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .navigation__topbar ul {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .navigation__topbar ul li {
    margin-left: 3.5rem;
  }
  .navigation__topbar ul li a {
    font-weight: 500;
    color: var(--white);
  }
  .navigation__topbar .navigation-btn {
    border: none;
    outline: none;
    font-family: var(--font-fraunces);
    font-size: 1.7rem;
    font-weight: var(--font-bold);
    text-transform: uppercase;
    padding: 1.6rem 2.3rem;
    border-radius: 30px;
    background-color: var(--white);
    margin-left: 3.5rem;
    cursor: pointer;
    transition: 400ms ease;
  }
  .navigation__topbar .navigation-btn:hover {
    background-color: rgba(255, 255, 255, 0.25);
    color: var(--white);
  }
  .navigation__hamburger {
    display: none;
  }
  .header__main h1 {
    font-size: 7rem;
    font-weight: 800;
    color: var(--white);
    letter-spacing: 5px;
    margin-top: 25rem;
    margin-bottom: 12.5rem;
  }
  .description__image {
    object-fit: contain;
  }
  .design_container {
    width: 100%;
    display: flex;
    flex-direction: row;
  }
  .graphic_design {
    width: 50%;
    object-fit: contain;
  }
  .photography {
    width: 50%;
    object-fit: contain;
  }
  .gallery {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: 1;
  }
}
@media only screen and (min-width: 64em) {
  .main_container .description_1 {
    display: flex;
    flex-direction: row-reverse;
    width: 100%;
  }
  .main_container .description_1 .description__image {
    width: 50%;
    height: 600px;
    background-image: url(../../images/desktop/image-transform.jpg);
    background-size: cover;
  }
  .main_container .description_1 .description__textbox {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    text-align: left;
    width: 50%;
    padding: 0rem 8rem;
  }
  .main_container .description_2 {
    display: flex;
    flex-direction: row;
  }
  .main_container .description_2 .description__image {
    width: 50%;
    height: 600px;
    background-image: url(../../images/desktop/image-stand-out.jpg);
    background-size: cover;
  }
  .main_container .description_2 .description__textbox {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    text-align: left;
    width: 50%;
    padding: 0rem 8rem;
  }
  .main_container .graphic_design {
    height: 640px;
    background-image: url(../../images/desktop/image-graphic-design.jpg);
    background-size: cover;
  }
  .main_container .testimonials {
    padding: 11rem 3rem 7rem;
  }
  .main_container .testimonials__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    column-gap: 4rem;
    padding: 0rem 3rem;
  }
  .main_container .testimonials__container .testimonials__card {
    width: 1/3;
  }
  .main_container .photography {
    height: 640px;
    background-image: url(../../images/desktop/image-photography.jpg);
    background-size: cover;
  }
  .main_container .gallery {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: 1;
  }
}